<template>
    <div class="page-notification">
        <div class="flex-v-center tool-bar"></div>
        <div style="width:98%;margin:20px auto;display:flex;padding-left:12px">
            <el-date-picker v-model="oneYearDate" :clearable="false" @change="oneYearChange" value-format="yyyy" type="year" placeholder=""></el-date-picker>
        </div>
        <div style="width:98%;margin:0 auto;display:flex">
            <!-- <el-date-picker v-model="yearDate" :clearable="false" @change="getYearTotal" value-format="yyyy" type="year" placeholder=""></el-date-picker> -->
            <div style="display: flex;padding-left: 20px; margin-left: 12px;color:#008eff;font-size:22px;">
                <p>{{totalNum}}</p>
                <p style="margin-left:20px;">{{totalPrice}}</p>
                <p style="margin-left:20px;">{{contractTotalNum}}</p>
                <p style="margin-left:20px;">{{contractTotalPrice}}</p>
            </div>
        </div>
        <div style="width:98%;margin:20px auto;display:flex;padding-left:1%">
            <el-date-picker v-model="fiveYearDate" :clearable="false" @change="getSaleFive" value-format="yyyy" type="year" placeholder=""></el-date-picker>
            <p style="color:#008eff">:{{saleFiveNum}}</p>
            <el-date-picker
                v-model="fiveYear2Date"
                :clearable="false"
                @change="getSale2Five"
                style="left:20%"
                value-format="yyyy-MM"
                type="month"
                placeholder=""
            ></el-date-picker>
            <p style="position: relative;left:23%;color:#008eff">:{{saleFive2Num}}</p>
        </div>
        <div style="width:98%;margin:20px auto;display:flex;padding-left:1%">
            <div id="saleFive" style="width:50%;height:300px;margin-top:20px;"></div>
            <div id="sale2Five" style="width:50%;height:300px;margin-top:20px;"></div>
        </div>
        <div style="width:98%;margin:20px auto;display:flex;padding-left:1%">
            <el-date-picker v-model="sixYearDate" :clearable="false" @change="getSaleSix" value-format="yyyy" type="year" placeholder=""></el-date-picker>
            <p style="color:#008eff">:{{salSixNum}}</p>
            <el-date-picker
                v-model="sixYear2Date"
                :clearable="false"
                style="left:20%"
                @change="getSale2Six"
                value-format="yyyy-MM"
                type="month"
                placeholder=""
            ></el-date-picker>
            <p style="position: relative;left:23%;color:#008eff">:{{salSix2Num}}</p>
        </div>
        <div style="width:98%;margin:20px auto;display:flex;padding-left:1%">
            <div id="saleSix" style="width:50%;height:300px;margin-top:20px;"></div>
            <div id="sale2Six" style="width:50%;height:300px;margin-top:20px;"></div>
        </div>

        <div style="width:90%;margin:20px auto;display:flex;padding-left:9%">
            <el-date-picker v-model="sevenYearDate" :clearable="false" @change="getBuyOne" value-format="yyyy" type="year" placeholder=""></el-date-picker>
        </div>
        <div style="width:90%;margin:20px auto;display:flex;padding-left:9%">
            <div id="saleSeven" style="width:90%;height:800px;margin-top:20px;"></div>
        </div>

        <div style="width:90%;margin:20px auto;display:flex;padding-left:9%">
            <el-date-picker v-model="yearDate" :clearable="false" @change="getYearTotal" value-format="yyyy" type="year" placeholder=""></el-date-picker>
        </div>
        <div style="height:500px;width:90%;margin:0 auto;">
            <div id="saleOne" style="width:80%;height:500px;margin:0 auto"></div>
        </div>
        <!-- <div style="width:90%;margin:20px auto;display:flex;padding-left:9%">
            <el-date-picker v-model="cYearDate" :clearable="false" @change="getCYearTotal" value-format="yyyy" type="year" placeholder=""></el-date-picker>
        </div>
        <div style="height:500px;width:90%;margin:0 auto;">
            <div id="saleThree" style="width:80%;height:500px;margin:0 auto"></div>
        </div>-->
        <div style="width:90%;margin:20px auto;display:flex;padding-left:9%">
            <el-date-picker v-model="twoYear" :clearable="false" @change="getSaleTwo" value-format="yyyy" type="year" placeholder=""></el-date-picker>
        </div>
        <div style="height:500px;width:90%;margin:0 auto;">
            <div id="saleTwo" style="width:80%;height:500px;margin:0 auto"></div>
        </div>
        <!-- <div style="height:500px;width:90%;margin:0 auto;">
            <div id="saleFour" style="width:80%;height:500px;margin:0 auto"></div>
        </div>-->
    </div>
</template>
<script>
export default {
    name: "xsreport-form",
    data() {
        return {
            chartOneList: [],
            yearDate: new Date().getFullYear() + "",
            oneYearDate: new Date().getFullYear() + "",
            cYearDate: new Date().getFullYear() + "",
            fiveYearDate: new Date().getFullYear() + "",
            sixYearDate: new Date().getFullYear() + "",
            twoYear: new Date().getFullYear() + "",
            sevenYearDate: new Date().getFullYear() + "",
            fiveYear2Date:
                new Date().getFullYear() +
                "-" +
                this.addRezo(new Date().getMonth() + 1),
            sixYear2Date:
                new Date().getFullYear() +
                "-" +
                this.addRezo(new Date().getMonth() + 1),
            totalNum: "",
            totalPrice: "",
            contractTotalNum: "",
            contractTotalPrice: "",
            saleFiveNum: 0,
            saleFive2Num: 0,
            salSixNum: 0,
            salSix2Num: 0
        };
    },
    created() {
        this.getTotal();
        this.getSaleFive();
        this.getSale2Five();
        this.getSaleSix();
        this.getSale2Six();
        this.getContractTotal();
        this.getSaleTwo(this.twoYear);
        // this.getSaleFour();
        this.getSaleOne(this.yearDate);
        // this.getCSaleOne(this.cYearDate);
        this.getBuyOne();
    },
    methods: {
        oneYearChange() {
            this.getTotal();
            this.getContractTotal();
        },
        addRezo(string) {
            return string > 9 ? string : "0" + string;
        },
        getTotal() {
            this.$http
                .get(`/haolifa/report/sale/getSaleAll?year=${this.oneYearDate}`)
                .then(res => {
                    this.totalNum = res[0].totalNum;
                    this.totalPrice = res[0].totalPrice;
                });
        },
        //
        getSaleFive() {
            this.saleFiveNum = 0;
            this.$http
                .get(
                    `/haolifa/report/sale/selectContractByDemandName?year=${this.fiveYearDate}`
                )
                .then(res => {
                    let nameData = [],
                        valueData = [];
                    res.map(item => {
                        nameData.push(item.demandName);
                        valueData.push({
                            name: item.demandName,
                            value: item.totalPrice
                        });
                        this.saleFiveNum = this.saleFiveNum + +item.totalPrice;
                    });
                    let chart = this.$echarts.init(
                        document.getElementById("saleFive")
                    );
                    let option = {
                        title: {
                            text: "",
                            // subtext: "",
                            x: "center"
                        },
                        tooltip: {
                            trigger: "item",
                            formatter: "{a} <br/>{b} : {c} ({d}%)"
                        },
                        legend: {
                            orient: "vertical",
                            left: "left",
                            data: nameData
                        },
                        series: [
                            {
                                name: "",
                                type: "pie",
                                radius: "55%",
                                center: ["50%", "60%"],
                                label: {
                                    show: false,
                                    position: "center"
                                },
                                data: valueData,
                                itemStyle: {
                                    emphasis: {
                                        shadowBlur: 10,
                                        shadowOffsetX: 0,
                                        shadowColor: "rgba(0, 0, 0, 0.5)"
                                    }
                                }
                            }
                        ]
                    };
                    chart.setOption(option);
                })
                .catch(e => {
                    this.$toast(e.msg || e.message);
                });
        },
        getSale2Five() {
            let arr = this.fiveYear2Date.split("-");
            console.log(this.fiveYear2Date, arr);
            let year = arr[0],
                month = arr[1];
            this.saleFive2Num = 0;
            this.$http
                .get(
                    `/haolifa/report/sale/selectContractByDemandName?year=${year}&month=${month}`
                )
                .then(res => {
                    let nameData = [],
                        valueData = [];
                    res.map(item => {
                        nameData.push(item.demandName);
                        valueData.push({
                            name: item.demandName,
                            value: item.totalPrice
                        });
                        this.saleFive2Num =
                            this.saleFive2Num + +item.totalPrice;
                    });
                    let chart = this.$echarts.init(
                        document.getElementById("sale2Five")
                    );
                    let option = {
                        title: {
                            text: "",
                            // subtext: "",
                            x: "center"
                        },
                        tooltip: {
                            trigger: "item",
                            formatter: "{a} <br/>{b} : {c} ({d}%)"
                        },
                        legend: {
                            orient: "vertical",
                            left: "left",
                            data: nameData
                        },
                        series: [
                            {
                                name: "",
                                type: "pie",
                                radius: "55%",
                                center: ["50%", "60%"],
                                label: {
                                    show: false,
                                    position: "center"
                                },
                                data: valueData,
                                itemStyle: {
                                    emphasis: {
                                        shadowBlur: 10,
                                        shadowOffsetX: 0,
                                        shadowColor: "rgba(0, 0, 0, 0.5)"
                                    }
                                }
                            }
                        ]
                    };
                    chart.setOption(option);
                })
                .catch(e => {
                    this.$toast(e.msg || e.message);
                });
        },
        //
        getSaleSix() {
            this.salSixNum = 0;
            this.$http
                .get(
                    `/haolifa/report/sale/selectshouhuiContractByDemandName?year=${this.sixYearDate}`
                )
                .then(res => {
                    let nameData = [],
                        valueData = [];
                    res.map(item => {
                        nameData.push(item.demandName);
                        valueData.push({
                            name: item.demandName,
                            value: item.totalPrice
                        });
                        this.salSixNum = this.salSixNum + +item.totalPrice;
                    });
                    let chart = this.$echarts.init(
                        document.getElementById("saleSix")
                    );
                    let option = {
                        title: {
                            text: "",
                            // subtext: "",
                            x: "center"
                        },
                        tooltip: {
                            trigger: "item",
                            formatter: "{a} <br/>{b} : {c} ({d}%)"
                        },
                        legend: {
                            orient: "vertical",
                            left: "left",
                            data: nameData
                        },
                        series: [
                            {
                                name: "",
                                type: "pie",
                                radius: "55%",
                                center: ["50%", "60%"],
                                data: valueData,
                                label: {
                                    show: false,
                                    position: "center"
                                },
                                itemStyle: {
                                    emphasis: {
                                        shadowBlur: 10,
                                        shadowOffsetX: 0,
                                        shadowColor: "rgba(0, 0, 0, 0.5)"
                                    }
                                }
                            }
                        ]
                    };
                    chart.setOption(option);
                })
                .catch(e => {
                    this.$toast(e.msg || e.message);
                });
        },
        getSale2Six() {
            this.salSix2Num = 0;
            let arr = this.sixYear2Date.split("-");
            let year = arr[0],
                month = arr[1];
            this.$http
                .get(
                    `/haolifa/report/sale/selectshouhuiContractByDemandName?year=${year}&month=${month}`
                )
                .then(res => {
                    let nameData = [],
                        valueData = [];
                    res.map(item => {
                        nameData.push(item.demandName);
                        valueData.push({
                            name: item.demandName,
                            value: item.totalPrice
                        });
                        this.salSix2Num = this.salSix2Num + +item.totalPrice;
                    });
                    let chart = this.$echarts.init(
                        document.getElementById("sale2Six")
                    );
                    let option = {
                        title: {
                            text: "",
                            // subtext: "",
                            x: "center"
                        },
                        tooltip: {
                            trigger: "item",
                            formatter: "{a} <br/>{b} : {c} ({d}%)"
                        },
                        legend: {
                            orient: "vertical",
                            left: "left",
                            data: nameData
                        },
                        series: [
                            {
                                name: "",
                                type: "pie",
                                radius: "55%",
                                center: ["50%", "60%"],
                                data: valueData,
                                label: {
                                    show: false,
                                    position: "center"
                                },
                                itemStyle: {
                                    emphasis: {
                                        shadowBlur: 10,
                                        shadowOffsetX: 0,
                                        shadowColor: "rgba(0, 0, 0, 0.5)"
                                    }
                                }
                            }
                        ]
                    };
                    chart.setOption(option);
                })
                .catch(e => {
                    this.$toast(e.msg || e.message);
                });
        },
        getContractTotal() {
            this.$http
                .get(
                    `/haolifa/report/sale/getSaleAllContract?year=${this.oneYearDate}`
                )
                .then(res => {
                    this.contractTotalNum = res[0].totalNum;
                    this.contractTotalPrice = res[0].totalPrice;
                });
        },
        getYearTotal() {
            this.getSaleOne(this.yearDate);
        },
        getCYearTotal() {
            this.getCSaleOne(this.cYearDate);
        },
        getSaleOne(date) {
            this.$http
                .get("/haolifa/report/sale/getSaleAllByMonth?year=" + date)
                .then(res => {
                    let xData = [],
                        yData = [];
                    Object.keys(res).forEach(key => {
                        xData.push(key + "");
                        yData.push(res[key]);
                    });
                    let chart = this.$echarts.init(
                        document.getElementById("saleOne")
                    );
                    let option = {
                        color: ["#3398DB"],
                        title: {
                            text: date + "",
                            // subtext: "",
                            x: "left"
                        },
                        tooltip: {
                            trigger: "item",
                            formatter: "{a} <br/>{b} : {c}"
                        },
                        xAxis: {
                            type: "category",
                            data: xData
                        },
                        yAxis: {
                            type: "value"
                        },
                        series: [
                            {
                                name: "",
                                barWidth: "30%",
                                data: yData,
                                type: "bar"
                            }
                        ]
                    };
                    chart.setOption(option);
                });
        },
        //
        getCSaleOne(date) {
            this.$http
                .get(
                    "/haolifa/report/sale/getSaleAllByMonthContract?year=" +
                        date
                )
                .then(res => {
                    let xData = [],
                        yData = [];
                    Object.keys(res).forEach(key => {
                        xData.push(key + "");
                        yData.push(res[key][0].totalPrice);
                    });
                    let chart = this.$echarts.init(
                        document.getElementById("saleThree")
                    );
                    let option = {
                        color: ["#3398DB"],
                        title: {
                            text: date + "",
                            // subtext: "",
                            x: "left"
                        },
                        tooltip: {
                            trigger: "item",
                            formatter: "{a} <br/>{b} : {c}"
                        },
                        xAxis: {
                            type: "category",
                            data: xData
                        },
                        yAxis: {
                            type: "value"
                        },
                        series: [
                            {
                                name: "",
                                barWidth: "30%",
                                data: yData,
                                type: "bar"
                            }
                        ]
                    };
                    chart.setOption(option);
                });
        },
        // -
        getSaleTwo(date) {
            this.$http
                .get(`/haolifa/report/sale/getSaleByModel?year=${date}`)
                .then(res => {
                    let xData = [],
                        yData = [];
                    res.map(item => {
                        xData.push(item.productModel);
                        yData.push(item.totalPrice);
                    });
                    let chart = this.$echarts.init(
                        document.getElementById("saleTwo")
                    );
                    let option = {
                        color: ["#3398DB"],
                        title: {
                            text: "",
                            // subtext: "",
                            x: "left"
                        },
                        tooltip: {
                            trigger: "item",
                            formatter: "{a} <br/>{b} : {c}"
                        },
                        xAxis: {
                            type: "category",
                            data: xData
                        },
                        yAxis: {
                            type: "value"
                        },
                        series: [
                            {
                                name: "",
                                barWidth: "30%",
                                data: yData,
                                type: "bar"
                            }
                        ]
                    };
                    chart.setOption(option);
                });
        },
        // -
        getSaleFour() {
            this.$http
                .get("/haolifa/report/sale/getSaleByModelContract")
                .then(res => {
                    let xData = [],
                        yData = [];
                    res.map(item => {
                        xData.push(item.productModel);
                        yData.push(item.totalPrice);
                    });
                    let chart = this.$echarts.init(
                        document.getElementById("saleFour")
                    );
                    let option = {
                        color: ["#3398DB"],
                        title: {
                            text: "",
                            // subtext: "",
                            x: "left"
                        },
                        tooltip: {
                            trigger: "item",
                            formatter: "{a} <br/>{b} : {c}"
                        },
                        xAxis: {
                            type: "category",
                            data: xData
                        },
                        yAxis: {
                            type: "value"
                        },
                        series: [
                            {
                                name: "",
                                barWidth: "30%",
                                data: yData,
                                type: "bar"
                            }
                        ]
                    };
                    chart.setOption(option);
                });
        },
        getBuyOne() {
            this.$http
                .get(
                    `/haolifa/report/sale/selectAllAmountByDemandName?year=${this.sevenYearDate}`
                )
                .then(res => {
                    let xData = [],
                        payTotal = [],
                        unpay = [],
                        total = [],
                        registered = [];
                    res.map(item => {
                        xData.push(item.demandName);
                        payTotal.push(item.saleAmount);
                        unpay.push(item.invoiceAmount);
                        total.push(item.deliveryAmount);
                        registered.push(item.refundAmount);
                    });
                    let that = this;
                    let chart = this.$echarts.init(
                        document.getElementById("saleSeven")
                    );
                    const option = {
                        title: {
                            text: "-",
                            x: "left"
                        },
                        tooltip: {
                            trigger: "axis",
                            axisPointer: {
                                // 
                                type: "shadow" // 'line' | 'shadow'
                            }
                        },
                        legend: {
                            data: [
                                "",
                                "",
                                "",
                                ""
                            ]
                        },
                        grid: {
                            left: "3%",
                            right: "4%",
                            bottom: "10%",
                            containLabel: true
                        },
                        xAxis: [
                            {
                                type: "category",
                                data: xData,
                                axisLabel: {
                                    rotate: 60
                                }
                            }
                        ],
                        yAxis: [
                            {
                                type: "value"
                            }
                        ],
                        series: [
                            {
                                name: "",
                                type: "bar",
                                barWidth: "10%",
                                data: payTotal
                            },
                            {
                                name: "",
                                type: "bar",
                                color: "#3398DB",
                                barWidth: "10%",
                                data: unpay
                            },
                            {
                                name: "",
                                type: "bar",
                                color: "#e6a23c",
                                barWidth: "10%",
                                data: total
                            },
                            {
                                name: "",
                                type: "bar",
                                color: "#67c23a",
                                barWidth: "10%",
                                data: registered
                            }
                        ]
                    };
                    chart.setOption(option);
                });
        }
    }
};
</script>
